.crypto-list-container {
  padding: 20px;
  text-align: center;
}

.search-bar {
  margin: 20px auto;
  display: flex;
  justify-content: center;
  gap: 10px;
}

.search-input {
  padding: 10px;
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.search-button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.crypto-table {
  margin-top: 20px;
}
