/* General styles for the registration page */
body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
  }
  
  .register-container {
    width: 100%;
    max-width: 400px;
    margin: 50px auto;
    background: #fff;
    padding: 20px 30px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .register-container h1 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  .register-container form {
    display: flex;
    flex-direction: column;
  }
  
  .register-container input,
  .register-container select {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 14px;
  }
  
  .register-container button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  .register-container button:hover {
    background-color: #0056b3;
  }
  
  .register-container .error-message {
    color: #ff4d4d;
    font-size: 14px;
    margin-bottom: 10px;
    text-align: center;
  }
  
  .register-container p {
    text-align: center;
    font-size: 14px;
    margin-top: 20px;
  }
  
  .register-container p a {
    color: #007bff;
    text-decoration: none;
  }
  
  .register-container p a:hover {
    text-decoration: underline;
  }
  