.crypto-search-container {
  padding: 20px;
  text-align: center;
}

.back-button {
  margin-bottom: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.coin-card {
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  text-align: center;
  width: 300px;
  background-color: #f9f9f9;
}

.coin-logo {
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}
